.modal-background {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .modal-content {
    background: white;
    padding: 2rem;
    position: relative;
  }
  
  .close-button {
    position: absolute;
    right: 1rem;
    top: 1rem;
    cursor: pointer;
  }